* {
  box-sizing: border-box;
}

body {
  background-color: #a9a9a9;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.search-tbar {
  width: 100%;
  height: 15%;
  background-color: #ff6b0a;
  padding-top: 5px;
  padding-bottom: 5px;
}

.search-form {
  width: 100%;
  justify-content: center;
  display: flex;
}

.search-form__input {
  width: 20%;
  font: inherit;
  background-color: #f8f8f8;
  border: none;
  border-radius: 4px 0 0 4px;
  outline: none;
  padding: 8px;
}

.search-form__button {
  width: 80px;
  color: #000;
  cursor: pointer;
  background-color: #c8c8c8;
  border: none;
  border-radius: 0 4px 4px 0;
  padding: 10px;
  font-size: 15px;
}

.search-form__button:hover, .search-form__button:focus {
  background-color: #b4b4b4;
  transition: all .5s;
}

.search-form:after {
  content: "";
  clear: both;
  display: table;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px 25px;
  display: flex;
}

.photo-card {
  flex-flow: column wrap;
  margin-right: 20px;
}

.photo {
  width: 320px;
  height: 250px;
  display: block;
}

.info {
  width: 320px;
  background-color: #f4a460;
  border-bottom: 1px solid #c3c2c2;
  border-left: 1px solid #c3c2c2;
  border-right: 1px solid #c3c2c2;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  justify-content: center;
  place-items: center;
  margin-bottom: 20px;
  font-size: 14px;
  display: flex;
  box-shadow: 0 3px 1px #0000001a, 0 1px 2px #00000014, 0 2px 2px #0000001f;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  padding: 0 9px;
  display: flex;
}

.info-item-api {
  padding-top: 5px;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #ff6b0a;
  border: none;
  border-radius: 5px;
  margin: 0 auto 20px;
  padding: 10px 20px;
  font-size: large;
  display: flex;
}

.load-more:hover, .load-more:hover {
  background-color: #98430a;
  transition: all .5s;
}

